<template>
  <div class="mt-2">
    <b-button block @click="showModal" variant="info">
      <i class="fas fa-mobile-alt"></i>
      PWAs
      <span class="float-right">
        <i class="fas fa-info-circle"></i>
      </span>
    </b-button>

    <b-modal ref="modalpwa" hide-footer title="PWAs - Progressive Web Apps">
      <div class="d-block">
        <p>
          <strong>PWAs</strong> são aplicações web com especificações que, em um cenário mobile, levam o usuário a ter uma
          experiência de uso semelhante a de um aplicativo nativo.
        </p>
        <p>Mas não são aplicativos híbridos, e também, não é apenas um site com aparência de aplicativo mobile.</p>
        <p>PWAs são aplicações webs com características que as fazem ser especiais, como:</p>
        <ul>
          <li>
            <strong>São progressivas</strong> - elas funcionam tanto em navegadores antigos com poucos recursos como são
            capazes de utilizar todos os recursos dos navegadores mais modernos;
          </li>
          <li>
            <strong>São responsivas</strong> - isto é, se adaptam a qualquer tamanho de tela, seja a tela de smartphone
            ou a de uma smartTV;
          </li>
          <li>
            <strong>São instaláveis</strong> - facilmente você pode instalar um pwa em seu dispositivo mobile e até
            mesmo em seu desktop;
          </li>
          <li>
            <strong>Funcionam offline</strong> - na minha opinião uma das maiores vantagens de um pwa é o fato de que
            ela pode ser usada offline, podendo ter acesso a todo conteúdo ou parte do conteúdo;
          </li>
          <li>
            <strong>Simples de Compartilhar -</strong> por serem aplicações web, podem ser facilmente compartilhadas por
            sua URL;
          </li>
          <li>
            <strong>Não tem a necessidade de serem baixadas ou instaladas</strong> - caso, você vai acessar uma única
            vez ou ficar um longo período sem acessar, você pode acessar normalmente por um navegador, como um site, sem
            a trabalheira de entrar na loja de aplicativos, baixar, instalar, usar apenas um vez e depois ter que
            desinstalar, para não ficar ocupando espaço em seu dispositivo.
          </li>
        </ul>
        <p>
          Na grande maioria das tecnologias web, você vai ouvir a famosa frase &quot;Mas a ... não é uma bala de
          prata.&quot; , mas na minha opinião se uma pwa não é uma bala de prata ela chega muito perto kkk.
        </p>
      </div>
      <b-button class="mt-3 float-right" variant="secondary" @click="hideModal">Fechar</b-button>
    </b-modal>
  </div>
</template>

<script>
export default {
  name: "Orms",
  methods: {
    showModal() {
      this.$refs.modalpwa.show();
    },
    hideModal() {
      this.$refs.modalpwa.hide();
    }
  }
};
</script>