<template>
  <div class="mt-2">
    <b-button block @click="showModal" variant="primary">
      <i class="fas fa-plug"></i>
      HTTP
      <span class="float-right">
       <i class="fas fa-info-circle"></i>
      </span>
    </b-button>

    <!-- Modal Component -->
    <b-modal ref="modalHttp" hide-footer title="HTTP Protocol">
      <div class="d-block">
        <p> <strong>HTTP</strong> é um protocolo que permite a obtenção de recursos, tais como documentos HTML. É a base de
        qualquer troca de dados na Web e um protocolo cliente-servidor, o que significa que as requisições são iniciados
        pelo destinatário, geralmente um navegador da Web. Um documento completo é reconstruído a partir dos diferentes
        sub-documentos obtidos, como por exemplo texto, descrição do layout, imagens, vídeos, scripts e muito mais.</p>
    <p>Clientes e servidores se comunicam trocando mensagens individuais (em oposição a um fluxo de dados). As mensagens
        enviadas pelo cliente, geralmente um navegador da Web, são chamadas de <strong>solicitações</strong>, ou também
        <strong>requisições</strong>, e as mensagens enviadas pelo servidor como resposta são chamadas de
        <strong>resposta</strong>.</p>
    <p>Projetado no início da década de 1990, o HTTP é um protocolo extensível que evoluiu ao longo do tempo. É um
        protocolo de camada de aplicação que é enviado sobre TCP, ou em uma conexão TCP criptografada com TLS embora
        qualquer protocolo de transporte confiável possa, teoricamente, ser usado. Devido à sua extensibilidade, ele é
        usado para não apenas buscar documentos de hipertexto, mas também imagens e vídeos ou publicar conteúdo em
        servidores, como nos resultados de formulário HTML . O HTTP também pode ser usado para buscar partes de
        documentos para atualizar páginas da Web sob demanda.</p>
    <p>Trecho retirado do site <strong>MDN web docs</strong>. <a
            href='https://developer.mozilla.org/pt-BR/docs/Web/HTTP/Overview' target='_blank'
            class='url'>https://developer.mozilla.org/pt-BR/docs/Web/HTTP/Overview</a></p>
    <p>&nbsp;</p>
      </div>
      <b-button class="mt-3 float-right" variant="secondary" @click="hideModal">Fechar</b-button>
    </b-modal>
  </div>
</template>

<script>
export default {
  name: "Http",
  methods: {
    showModal() {
      this.$refs.modalHttp.show();
    },
    hideModal() {
      this.$refs.modalHttp.hide();
    }
  }
};
</script>