<template>
  <div class="mt-2">
    <b-button block @click="showModal" variant="info">
      <i class="far fa-file"></i>
      SPAs
      <span class="float-right">
        <i class="fas fa-info-circle"></i>
      </span>
    </b-button>

    <b-modal ref="modalspa" hide-footer title="SPA ou Single Page Aplication">
      <div class="d-block">
        <p>
          <strong>Single Page Aplication</strong> ou em português
          <strong>Aplicação de uma página só</strong> é uma
          poderosa e moderna tecnologia, que, como o nome já diz, possibilita desenvolvermos toda a nossa aplicação web,
          em apenas uma página .
        </p>
        <p>Como assim? E quando eu precisar mostrar outras telas ou baixar conteúdo do servidor?</p>
        <p>
          Essa tecnologia utiliza diversos recursos do JavaScript e dos navegadores para poder modificar os componentes da
          página e fazer solicitações ao servidor através de requisições HTTP. Assim, possibilitando a apresentação de
          outras telas e dados vindo do servidor sem a necessidade de recarregar a página ou abrir uma nova página. O
          JavaScript se encarrega de atualizar apenas os elementos necessários para mostrar uma nova tela.
        </p>
        <p>
          <strong>E qual a vantagem disso?</strong>
        </p>
        <p>
          Além da excelente
          <strong>experiência do usuário</strong>, essa prática gera uma notável diminuição do consumo de
          dados vindo do servidor, o que é excelente para
          <strong>economia dos dados móveis dos usuários</strong> e para a
          <strong>economia dos recursos dos servidores</strong>, que não precisam mais ficar processando páginas inteiras
          e enviando para o cliente.
        </p>
        <p>
          Por essas e outras grandes vantagens as SPAs vem adquirindo um grande número de adeptos entre os desenvolvedores
          e empresas de desenvolvimento web.
        </p>
      </div>
      <b-button class="mt-3 float-right" variant="secondary" @click="hideModal">Fechar</b-button>
    </b-modal>
  </div>
</template>

<script>
export default {
  name: "spa",
  methods: {
    showModal() {
      this.$refs.modalspa.show();
    },
    hideModal() {
      this.$refs.modalspa.hide();
    }
  }
};
</script>