<template>
  <div>
    <b-button block @click="showModal" variant="primary">
      <i class="fas fa-project-diagram"></i>
      API
      <span class="float-right">
       <i class="fas fa-info-circle"></i>
      </span>
    </b-button>

    <!-- Modal Component -->
    <b-modal ref="modalApi" hide-footer title="APIs">
      <div class="d-block">
      <p>
        A sigla
        <strong>API</strong> corresponde às palavras em inglês “
        <strong>Application Programming</strong> 
        <strong> Interface</strong>“, no português “Interface de Programação de Aplicações”.
      </p>
      <p>
       <strong>API</strong> é uma aplicação desenvolvida para operar, funcionar e atuar com 
        outras aplicações, de diversos segmentos, fornecendo os recursos necessários para 
        que elas executem suas funções. 
        De forma segura, ágil, prática, sem a necessidade de conhecer detalhes de implementação e 
        nem mesmo usarem a mesma linguagem de programação.
      </p>
      <p>
        Hoje, é muito comum desenvolvedores e empresas de desenvolvimento criarem APIs para atender as
