      </p>
      </div>
      <b-button class="mt-3 float-right" variant="secondary" @click="hideModal">Fechar</b-button>
    </b-modal>
  </div>
</template>

<script>
export default {
  name: "Api",
  methods: {
    showModal() {
      this.$refs.modalApi.show();
    },
    hideModal() {
      this.$refs.modalApi.hide();
    }
  }
};
</script>